import React, {Component} from 'react';
import './Assets/css/ds.min.css'

class About extends Component {
    render() {
        return (
            <div className = "myabout">
               <h1>
                    About Me
                </h1>
                <img className = "me" style = {{float: "left"}} src={require('./Me.JPG')} alt="SVM" ></img>
                <p className = "center">
                    I am a computer programmer and machine learning specialist who just graduated from UCSD. I have strong knowledge in both object oriented programming languages and scripting languages such as Python. I love finding new problems with big data models, and solving problems through coding. If you're looking for a software engineer, please stick around and check out my portfolio.
                </p>
            </div>
        );
    }
}

export default About;