import React, {Component} from 'react';

class DataSci extends Component {
    render() {
        return (
            <div className = "dataProj">
               <h1>
                    Data Science 
                </h1>
                <h2>
                    Heart Disease Predictor
                </h2>
                <img className = "ds" src={require('./bestfeatures.PNG')} alt="best features"></img>
                <img className = "ds" src={require('./KNN.PNG')} alt="KNN"></img>
                <img className = "ds" src={require('./SVM.PNG')} alt="SVM"></img>
                <p>
                    Here I worked in a group to predict heart disease using different machine learning models. Our goal was to find which combination of factors are directly correlated with heart disease using data from the UC Irvine Machine Learning Repository. We started by organizing the features by correlation to heart disease and normalized the data on a z-score transformation. We then trained this processed data on 4 different classifiers: Random Forest, KNN, Logistic Regression, and SVM Linear Kernel. We found that all models pointed to using between the to 3 and 4 features. Adding more did not help the prediction much, indicating the importance of the too 4 characteristics. 
                </p>
                <h2>
                    Data Project 2 template
                </h2>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                </p>
            </div>
        );
    }
}

export default DataSci;